
          @import "@/assets/styles/_index.scss";
        
.icon {

  path:nth-child(1n) {
    animation: sparkle 0.3s linear infinite alternate;
  }

  path:nth-child(3n) {
    animation: sparkle 0.3s 0.6s linear infinite alternate;
  }

  path.icon__js {
    animation: none;
  }
}

@keyframes sparkle {
  100% {
    fill: #b0e0e6;
  }
}