
          @import "@/assets/styles/_index.scss";
        
.icon {
  transform-origin: bottom;
  animation: bounce 3s linear infinite;
}

@keyframes bounce {
  0%,
  20%,
  50%,
  80%,
  100% {
    transform: translateY(0);
  }
  40% {
    transform: translateY(-1.95rem);
  }
  60% {
    transform: translateY(-0.3rem);
  }
}
